import youbikeLogo from "icons/youbike_logo.svg";
import { useNavigate } from "react-router-dom";
import { useState } from "react";
import hamburger from "icons/hamburger.svg";
import cross from "icons/cross.svg";
import styles from "./Header.module.scss";

export default function Header() {
  const navigate = useNavigate();
  const [headerMenuButton, setHeaderMenuButton] = useState(hamburger);
  return (
    <div className={styles.header}>
      <div className={styles.headerLeft}>
        <img
          className={styles.youbikeLogo}
          src={youbikeLogo}
          alt="youbike_logo"
          onClick={() => {
            navigate("/");
          }}
        />
        <ul className={styles.navItem}>
          <li onClick={() => navigate("/youbike/manual")}></li>
          <li onClick={() => navigate("/youbike/pay-method")}></li>
          <li onClick={() => navigate("/youbike/station-info")}></li>
          <li onClick={() => navigate("/youbike/news")}></li>
          <li onClick={() => navigate("/youbike/events")}></li>
        </ul>
      </div>
      <div className={styles.signinButton}></div>
      <input
        type="checkbox"
        id="hamburgerToggle"
        className={styles.hamburgerToggle}
        onChange={(e) => {
          const isHeaderMenuShow = e.target.checked;
          if (isHeaderMenuShow) {
            setHeaderMenuButton(cross);
          } else {
            setHeaderMenuButton(hamburger);
          }
        }}
      />
      <label htmlFor="hamburgerToggle" className={styles.label}>
        <img
          className={styles.hamburger}
          src={headerMenuButton}
          alt="hamburger"
        />
      </label>
      <ul className={styles.mobileNavItem}>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
  );
}
